// `https://wordpress.altos.dev/wp-json/wp/v2/categories?post=${quoteID}`

/**
 * Data Needed
 * ------------------
 * 
 * - Title            (X)
 * - Publish Date     (X)
 * - Category         (X)
 * - Featured Image   ( )
 * - Post Content     (X)
 * 
 */

import 'core-js/stable';
import 'regenerator-runtime/runtime';

const API_URL = 'https://wordpress.altos.dev/wp-json/wp/v2/posts';
const CAT_URL_BASE = 'https://wordpress.altos.dev/wp-json/wp/v2/categories?post=';
const MEDIA_URL_BASE = 'https://wordpress.altos.dev/wp-json/wp/v2/media?parent=';

const quoteList = document.getElementsByClassName('row quote-list');

const formatDate = timeStamp => {
  // Create a date object from the timestamp
  let date = new Date(timeStamp);

  // Create a list of names for the months
  let months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

  // return a formatted date
  return months[date.getMonth()] + ' ' + date.getDate() + ', ' + date.getFullYear();
}

const listQuote = quote => {
  let container = quoteList[0];

  container.innerHTML += `
  <article id="quote quote-${quote.id}">
  <ul>
    <li>${quote.title}</li>
    <li>${quote.categoryName}</li>
    <li>${quote.date}</li>
    <li>${quote.imgURL}</li>
  </ul>

  </article>
  `;

}



